import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './components/layout.component';

const routes: Routes = [
    {
        path: '', component: LayoutComponent,
        children: [
            { path: 'dashboard', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\dashboard\\dashboard.module')['DashboardModule']);  });}) },
            { path: 'myprofile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\myprofile\\myprofile.module')['MyprofileModule']);  });}) },
            { path: 'employee', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\employee\\employee.module')['EmployeeModule']);  });}) },
            { path: 'appraisal', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\appraisal\\appraisal.module')['AppraisalModule']);  });}) },
            { path: 'lookup', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\lookup\\lookup.module')['LookupModule']);  });}) },
            { path: 'crm', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\crm\\crm.module')['CrmModule']);  });}) },
            { path: 'recruitment', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\recruitment\\recruitment.module')['RecruitmentModule']);  });}) },
            { path: 'leaveandattendance', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\leaveandattendance\\leaveandattendance.module')['LeaveandattendanceModule']);  });}) },
            { path: 'payroll', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\payroll\\payroll.module')['PayrollModule']);  });}) },
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class LayoutRoutingModule { }



